const getBackgroundColor = (days) => {
    if (days <= 0) return '#2E2E2E'; // Negro (oscuro)
    if (days <= 30) return '#B00000'; // Rojo (oscuro)
    if (days > 30 && days <= 120) return '#F1D100'; // Amarillo
    if (days > 120) return '#6BAF59'; // Verde
};

export default getBackgroundColor;
