import './SistemaPotencia.css';
import SelectContentBox from './SelectContentBox';
import img1 from './assets/img/M-G015.jpg';
import img2 from './assets/img/SP-005.jpg';
import img3 from './assets/img/SP-008.jpg';


const items=[
    { title: 'Unidad de potencia', img: [img1], text: '<p>El mantenimiento de una Unidad de Potencia es crucial para asegurar su correcto funcionamiento y evitar fallas en los sistemas que alimenta. A continuación, se describe el proceso de mantenimiento típico:</p><ul>Inspección visual general: Se revisa todo el sistema en busca de posibles fugas, corrosión, acumulación de suciedad o partículas contaminantes, y se asegura que todas las conexiones estén firmes y sin daños.</ul><ul>Revisión del nivel y estado del fluido hidráulico: El fluido es el alma del sistema hidráulico. Se revisa el nivel del aceite y su calidad, verificando si ha perdido sus propiedades (viscosidad, color o presencia de partículas). En caso de contaminación o deterioro, se procede al reemplazo.</ul><ul>Cambio de filtros: Los filtros hidráulicos se encargan de mantener el sistema libre de contaminantes. Se revisan y, si es necesario, se sustituyen los filtros de aceite, de aire y de retorno para asegurar un flujo limpio.</ul><ul>Inspección y prueba de la bomba hidráulica: Se comprueba el estado de la bomba para asegurarse de que genera la presión adecuada y no tiene fugas. Si se detectan ruidos inusuales o un rendimiento deficiente, puede ser necesario su mantenimiento o reemplazo.</ul><ul>Revisión de las válvulas: Se inspeccionan las válvulas de control, presión y dirección para garantizar que están funcionando correctamente. Esto incluye la limpieza o el reemplazo si muestran signos de desgaste o mal funcionamiento.</ul><ul>Verificación del motor eléctrico o diésel: Si la HPU está accionada por un motor, se realiza una revisión completa de este componente, verificando los niveles de aceite, el estado de los filtros de combustible y aire, y los componentes eléctricos para garantizar su correcto arranque y operación.</ul><ul>Inspección de los acumuladores: Si la unidad tiene acumuladores hidráulicos, se verifica la presión interna y se revisa su estado estructural para asegurar que puedan almacenar y liberar energía sin problemas.</ul><ul>Revisión de los manómetros y sensores: Se comprueba que los instrumentos de medición, como manómetros y sensores de temperatura y presión, estén funcionando correctamente para monitorear el rendimiento de la unidad de potencia.</ul><ul>Pruebas de presión y flujo: Después del mantenimiento, se realizan pruebas de rendimiento para asegurar que la unidad esté generando la presión y el flujo requeridos según las especificaciones operativas.</ul><p>Este proceso de mantenimiento preventivo asegura que la Unidad de Potencia Hidráulica funcione eficientemente, reduciendo el riesgo de fallas y prolongando la vida útil de los equipos conectados.</p>'},
    { title: 'Cilindro Hidráulico', img: [img1], text: '<p>El mantenimiento de un cilindro hidráulico es fundamental para garantizar su óptimo rendimiento y evitar fallas en los sistemas donde se utiliza. A continuación, se describen los pasos típicos en el proceso de mantenimiento:</p><ul>Inspección visual: Se realiza una revisión exhaustiva del cilindro hidráulico para detectar posibles fugas de aceite, daños en la superficie del vástago o señales de desgaste en las conexiones y juntas.</ul><ul>Desmontaje del cilindro: Se procede a desmontar el cilindro hidráulico para evaluar el estado de los componentes internos. Esto incluye la extracción del vástago, pistón, sellos y otras partes móviles.</ul><ul>Limpieza interna y externa: Todas las piezas desmontadas se limpian a fondo para eliminar residuos de aceite, polvo, partículas metálicas o cualquier tipo de contaminación que pueda interferir con el funcionamiento.</ul><ul>Inspección del vástago y pistón: Se examina el vástago en busca de rayones, abolladuras o corrosión que puedan comprometer el sellado. También se verifica el estado del pistón para asegurarse de que esté en condiciones óptimas.</ul><ul>Reemplazo de sellos y juntas: Los sellos y juntas son componentes críticos para mantener la estanqueidad del sistema hidráulico. Se reemplazan si muestran signos de desgaste, endurecimiento o deformación.</ul><ul>Revisión del alojamiento: Se verifica que el alojamiento del cilindro no presente deformaciones o desgaste excesivo, ya que esto podría causar fugas o fallos en el funcionamiento.</ul><ul>Montaje del cilindro: Una vez inspeccionados y reemplazados los componentes dañados, se vuelve a montar el cilindro, asegurándose de que todas las piezas estén correctamente alineadas y ajustadas.</ul><ul>Pruebas de presión y funcionamiento: Finalmente, se somete el cilindro hidráulico a pruebas de presión para verificar que no haya fugas y que funcione adecuadamente bajo las condiciones de trabajo.</ul><p>Este proceso de mantenimiento preventivo asegura que el cilindro hidráulico opere de manera eficiente, prolongando su vida útil y evitando problemas en el sistema hidráulico en el que está instalado.</p>'},
    { title: 'Circuitos Hidráulicos', img: [img1], text: '<p>El mantenimiento de los circuitos hidráulicos es esencial para garantizar su funcionamiento eficiente y prolongar la vida útil del sistema. Un mantenimiento adecuado previene fallas, reduce tiempos de inactividad y optimiza la operación de los equipos. A continuación, se detallan los pasos generales del mantenimiento a circuitos hidráulicos:</p><ul>Inspección visual general del sistema: Se revisan todas las líneas, conexiones y componentes del circuito en busca de fugas de aceite, daños en las tuberías, corrosión o conexiones sueltas. Las fugas en los sistemas hidráulicos pueden ser indicativas de problemas graves en el futuro si no se corrigen a tiempo.</ul><ul>Revisión de los filtros: Se inspeccionan y, si es necesario, se sustituyen los filtros del sistema hidráulico. Los filtros juegan un papel crucial en la eliminación de contaminantes del fluido hidráulico que podrían dañar otros componentes del circuito.</ul><ul>Verificación del fluido hidráulico: Se revisa el nivel y la calidad del aceite hidráulico. Es fundamental que el fluido esté limpio y con las propiedades correctas para un funcionamiento óptimo. Si el fluido ha perdido sus características de viscosidad o está contaminado, es necesario su reemplazo.</ul><ul>Revisión de las mangueras y tuberías: Las mangueras y tuberías del circuito hidráulico se revisan en busca de signos de desgaste, hinchazón o grietas. Si alguna de las mangueras o tuberías muestra señales de daño, debe ser reemplazada para evitar fallos y fugas.</ul><ul>Inspección de bombas y motores: Las bombas y motores son componentes clave en un circuito hidráulico. Se verifica que no haya ruidos inusuales, pérdidas de presión o flujo insuficiente. El desgaste de estos componentes puede provocar una reducción en la eficiencia del sistema y aumentar los costos operativos.</ul><ul>Verificación de válvulas: Se revisan las válvulas de control de presión, dirección y flujo. Estas deben operar de forma suave y sin bloqueos. Cualquier mal funcionamiento en las válvulas puede afectar el rendimiento del circuito.</ul><ul>Revisión de los cilindros hidráulicos: Los cilindros se inspeccionan en busca de fugas, golpes o deformaciones. El sellado de los cilindros debe estar en buen estado para evitar fugas internas o externas que puedan comprometer la fuerza del sistema.</ul><ul>Pruebas de presión y flujo: Se realizan pruebas para comprobar que la presión y el flujo en el circuito hidráulico se encuentran dentro de los parámetros establecidos. Esto permite identificar cualquier pérdida de rendimiento o anomalía que podría necesitar ajustes o reparaciones.</ul><ul>Lubricación de componentes móviles: Los componentes móviles, como juntas o cilindros, deben ser lubricados según las especificaciones del fabricante para evitar el desgaste prematuro.</ul><ul>Comprobación de sensores y manómetros: Se revisa el correcto funcionamiento de los sensores de presión y los manómetros, asegurándose de que proporcionan lecturas precisas para el control adecuado del circuito.</ul><ul>Pruebas de funcionamiento: Una vez finalizado el mantenimiento, se realizan pruebas de funcionamiento para verificar que todo el circuito esté operando de manera eficiente y sin fallas.</ul><p>Este proceso de mantenimiento garantiza la eficiencia y confiabilidad del circuito hidráulico, minimizando el riesgo de fallas y mejorando el rendimiento general del sistema.</p>'},
    { title: 'Spoolers', img: [img2, img3], text: '<p>El mantenimiento de un spooler, así como de sus componentes principales como el motor hidráulico, la bomba hidráulica y las válvulas, es esencial para garantizar su óptimo funcionamiento y prolongar su vida útil. Este proceso incluye las siguientes acciones:</p><ul>Inspección general del spooler: Se revisa la estructura para detectar signos de desgaste, corrosión o daños físicos que puedan comprometer su operación.</ul><ul>Mantenimiento del motor hidráulico: Se inspeccionan las conexiones, se limpian los componentes, y se verifica el estado del aceite hidráulico para asegurar una correcta lubricación. Además, se chequean sellos y juntas para prevenir fugas.</ul><ul>Revisión de la bomba hidráulica: Se inspecciona la bomba hidráulica para garantizar que no haya fallas en el flujo del fluido. Esto incluye verificar la presión de salida, revisar el nivel del aceite hidráulico y asegurarse de que no haya contaminación o partículas en el fluido.</ul><ul>Inspección y ajuste de válvulas: Las válvulas son clave para controlar el flujo y la presión en el sistema hidráulico del spooler. Se realiza una prueba de presión para asegurarse de que las válvulas abran y cierren correctamente, y se ajustan si es necesario.</ul><ul>Pruebas de funcionamiento: Tras las inspecciones y ajustes, se realizan pruebas operativas del spooler bajo condiciones controladas para asegurarse de que todas las piezas trabajen en conjunto de manera eficiente y sin problemas.</ul><p>Este mantenimiento preventivo permite asegurar que el spooler mantenga un rendimiento confiable, reduciendo tiempos de inactividad no planificados y mejorando la eficiencia del equipo en operaciones críticas.</p>'}
]

function SistemaPotencia() {
  return (
   <div className='Container-SistemaPotencia'>
    <h2>Mantenimiento a Sistemas Hidraulicos de Potencia</h2>
    <SelectContentBox  
        items={items}
        />
   </div>
  );
}

export default SistemaPotencia;
