   { title: 'Puesta en marcha', img: [img17, img18, img4], text: '<p>Nuestro equipo de especialistas se encarga de garantizar una transición fluida y efectiva a la operación del sistema.</p><h3>Importancia de la Puesta en Marcha de VDF:</h3><ul>Operación segura: Aseguramos que el sistema esté completamente preparado para funcionar de manera segura, minimizando los riesgos asociados con la operación inicial.</ul><ul>Ajuste de parámetros finales: Durante la puesta en marcha, ajustamos los parámetros del VDF para optimizar su rendimiento en función de las necesidades específicas del cliente.</ul><ul>Capacitación al personal: Proporcionamos capacitación al personal del cliente para garantizar que operen el sistema de manera efectiva y segura.</ul><ul>Verificación de desempeño: Realizamos una serie de pruebas para verificar que el sistema funcione según lo previsto, asegurando su eficiencia y confiabilidad.</ul><h3>Fases de nuestro Servicio de Puesta en Marcha:</h3><ul>Revisión final de la instalación: Comprobamos que todas las conexiones y configuraciones estén correctas antes de iniciar el proceso.</ul><ul>Configuración de parámetros: Ajustamos los parámetros del VDF según las especificaciones del cliente y las condiciones de operación.</ul><ul>Pruebas de funcionamiento: Realizamos pruebas exhaustivas para asegurar que el sistema opere correctamente bajo diferentes condiciones de carga.</ul><ul>Documentación y entrega: Proporcionamos un informe detallado de la puesta en marcha, incluyendo recomendaciones para el mantenimiento futuro y un resumen de las pruebas realizadas.</ul><p>Con nuestros servicios de puesta en marcha de VDF de media y baja tensión, garantizamos una integración exitosa y eficiente de sus sistemas eléctricos, permitiendo a nuestros clientes maximizar su rendimiento desde el primer día.</p>'},
   { title: 'Mantenimiento', img: [img9, img6, img7, img8], text: '<p>Nuestro equipo especializado ofrece servicios completos de mantenimiento preventivo y correctivo para Variadores de Frecuencia (VDF) de media y baja tensión, asegurando su funcionamiento óptimo y prolongando su vida útil.</p><h3>Importancia del Mantenimiento de VDF:</h3><ul>Prevención de fallas: Un mantenimiento regular reduce el riesgo de fallos inesperados que puedan afectar la operación del sistema, evitando costosas reparaciones y tiempos de inactividad.</ul><ul>Optimización del rendimiento: Con el ajuste periódico de parámetros y la revisión de componentes, garantizamos que el VDF continúe operando de manera eficiente, maximizando el ahorro energético y el rendimiento del motor.</ul><ul>Detección temprana de problemas: A través de inspecciones detalladas, identificamos problemas potenciales antes de que se conviertan en fallos mayores, protegiendo tus sistemas eléctricos.</ul><ul>Cumplimiento normativo: Aseguramos que los VDF cumplan con las normativas de seguridad y eficiencia aplicables, protegiendo al personal y asegurando la integridad del sistema.</ul><h3>Fases de nuestro Servicio de Mantenimiento:</h3><ul>Inspección inicial: Verificamos el estado general del VDF, incluyendo sus conexiones eléctricas y el estado físico de los componentes.</ul><ul>Limpieza y verificación de componentes: Realizamos la limpieza de los elementos internos y revisamos el estado de los ventiladores, filtros, disipadores y otros componentes críticos.</ul><ul>Revisión de parámetros: Analizamos los ajustes del VDF para asegurar que estén optimizados según las condiciones de operación actuales.</ul><ul>Pruebas funcionales: Verificamos el rendimiento del VDF bajo diferentes condiciones de carga para asegurarnos de su correcto funcionamiento.</ul><ul>Informe detallado: Proporcionamos un informe completo con las actividades realizadas y recomendaciones para futuras intervenciones o ajustes.</ul><p>Con nuestro servicio de mantenimiento de VDF, aseguramos que tus sistemas continúen funcionando de manera eficiente y sin interrupciones, permitiéndote concentrarte en las operaciones diarias.</p>'}
